.Home {
  overflow: hidden;
  width: 100%;
  min-height: 115vh;

  position: relative;
  pointer-events: none;

  .home-canvas {
    pointer-events: all;
    position: absolute !important;
    top: 0;
    left: 0;
    height: 100%;
    width: 100vw;
  }
  .title {
    z-index: 10;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, calc(-50% - 5vh));
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    // padding: 2rem;
    // transform: translateY(-5vh);

    h1 {
      word-wrap: break-word;
      white-space: nowrap;
      margin: auto;
      margin-top: 0;
      backdrop-filter: blur(2px);
      font-weight: 400;
      font-size: clamp(1.7rem, 10vw, 3rem);
      letter-spacing: -3px;
      max-width: 45rem;
      line-height: 100%;
      text-shadow: 0px 0px 12px rgba(0, 0, 0, 0.3);
      margin-bottom: 0;
      transition: 1.5s ease-in-out transform, 1.5s ease-in-out opacity;
    }
    h1.fade-right {
      opacity: 0;
      transform: translateX(50%);
    }
    h1.fade-left {
      opacity: 0;
      transform: translateX(-50%);
    }

    h2 {
      padding-top: 0.5rem;
      margin: auto;
      margin-top: 0;
      backdrop-filter: blur(2px);
      font-weight: 200;
      font-size: 1.25rem;
      text-shadow: 0px 0px 12px rgba(0, 0, 0, 0.5);
      opacity: 0;
      animation: fade-in-out 3s ease forwards;
    }
    h2.fade {
      opacity: 0;
    }
  }
}

@keyframes fade-in-out {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.7;
  }
}
